/* styles.css */

.register-page {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.register-page::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("https://images.unsplash.com/photo-1593538312308-d4c29d8dc7f1?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(3px);
    z-index: -1; /* Send the pseudo-element to the background */
}

.register-container {
    position: relative;
    z-index: 1; /* Bring the container to the foreground */
    background-color: #ffffff; /* White background */
    padding: 20px; /* Adjusted padding */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
    width: 50%; /* Adjusted width */
}


.register-form {
    padding: 30px;
}

.register-form h1 {
    margin-top: 20px;
    text-align: center;
    font-weight: bold;
    color: #2c3e50; /* Darker text color */
}

.register-form a {
    color: #3498db !important; /* Blue link color */
}

.register-form a:hover {
    color: #2980b9 !important; /* Darker blue on hover */
}

.register-form button {
    background-color: #3498db; /* Blue button color */
    color: #ffffff; /* White text color */
    border: none;
    border-radius: 5px; /* Rounded button corners */
    padding: 10px 20px; /* Smaller padding */
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease; /* Smooth hover transition */
}

.register-form button:hover {
    background-color: #2980b9; /* Darker blue on hover */
}

.register-form input:focus {
    border: 1px solid #3498db !important;
}

.subtext {
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    margin: 10px 0; /* Adjusted margin */
    color: #666;
}

  
