/* .login-page styles */
.login-page {
    height: 100vh !important;
    width: 100vw !important;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: #f5f5f5; /* Light background color */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
}

/* .login-form styles */
.login-form {
    background-color: #ffffff; /* White background */
    padding: 30px;
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
}

.login-form h1 {
    text-align: center;
    font-weight: bold;
    margin: 20px 0; /* Smaller top and bottom margin */
    color: #2c2f50; /* Darker text color */
}

.login-form .ant-form-item-label label {
    color: #34495e !important; /* Darker label color */
}

.login-form button {
    background-color: #3498db; /* Blue button color */
    color: #ffffff; /* White text color */
    border: none;
    border-radius: 5px; /* Rounded button corners */
    padding: 10px 20px; /* Smaller padding */
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease; /* Smooth hover transition */
}

.login-form button:hover {
    background-color: #2980b9; /* Darker blue on hover */
}

.login-form a {
    color: #3498db !important; /* Blue link color */
}

.login-form a:hover {
    color: #2980b9 !important; /* Darker blue on hover */
}


.heading-container {
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .heading {
    font-size: 2.5rem;
